@import url("https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inder:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@600&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-inter: Inter;
  --font-rubik: Rubik;
  --font-inder: Inder;
  --font-montserrat: Montserrat;

  /* font sizes */
  --font-size-base: 16px;
  --font-size-xl: 20px;
  --font-size-lg: 18px;
  --font-size-10xl: 29px;
  --font-size-21xl: 40px;
  --font-size-6xl: 25px;
  --font-size-16xl: 35px;
  --font-size-26xl: 45px;
  --font-size-11xl: 30px;

  /* Colors */
  --color-powderblue-100: #c3e0d7;
  --color-powderblue-200: #9acbbd;
  --color-powderblue-300: rgba(154, 203, 189, 0);
  --color-black: #000;
  --color-white: #fff;
  --color-gray-100: #8b8b8b;
  --color-gray-200: #808080;
  --color-gray-300: #23191a;
  --color-gray-400: rgba(255, 255, 255, 0);
  --color-darkgray-100: #b3b3b3;
  --color-darkgray-200: #aaa2a2;
  --color-gainsboro: #d9d9d9;

  /* Gaps */
  --gap-3xs: 10px;

  /* Paddings */
  --padding-xs: 12px;
  --padding-33xl: 52px;
  --padding-4xl: 23px;
  --padding-13xl: 32px;
  --padding-sm: 14px;

  /* border radiuses */
  --br-81xl: 100px;
  --br-8xs: 5px;
  --br-11xl: 30px;
  --br-xs: 12px;
  --br-5xs: 8px;
  --br-9xl: 28px;
  --br-41xl: 60px;
}
